import { Card, CardContent, Container, Typography } from "@material-ui/core";
import { CSSProperties } from "react";
import { PJ_OBJ } from "../util/constants";

interface PJCardProps{
    pj_tupple: {pj:PJ_OBJ,selected: boolean, active: boolean}
}
export default function PJCard(props:PJCardProps){
    const {pj_tupple} = props
    let pj = pj_tupple.pj

    const cardStyle: CSSProperties={
      
        background: pj_tupple.active? 'gray': pj_tupple.selected? 'lightgray': 'white'
    }
    return(
       
        <Card variant="outlined" style={cardStyle} key={pj.id+'card'}>
            <CardContent>
                <Typography>
                    {pj.name? pj.name: ''}
                </Typography>
                <Typography>
                ({pj.id})
                </Typography>
            </CardContent>
        </Card>
       
       
    )
}